body {
  background-color: #343d46;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont,"Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body h1, h2{
  text-align: center;
}

/*Grid setup and containers*/
.imageSearch{
  grid-area: imageSearch;
}
.imagePlacard{
  grid-area: imagePlacard;
}
.imageSaved{
  grid-area: imageSaved;
}
.actionMenu{
  grid-area: actionMenu;
}
.grid-container{
  display: grid;

  grid-template-areas:
'imageSearch imageSearch imageSearch imageSearch imageSearch imageSearch actionMenu'
'imagePlacard imagePlacard imagePlacard imagePlacard imagePlacard imagePlacard imageSaved';
  background-color: #343d46;
  grid-gap: 25px;
}

