.imagePlacards-container{
  display:grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 5px;
}

.actionMenu-container{
  display:grid;
  grid-template-columns: auto;
  text-align: center;
}

.imageSaved-container{
  display:grid;
  grid-template-columns: auto auto;
  grid-gap: 2px;
}

.grid-item{
  color: Snow;
}

@media only screen and (max-width: 800px) {
  .grid-container{
    grid-template-areas:
    'imageSearch'
    'actionMenu'
    'imageSaved'
    'imagePlacard'
    ;
    background-color: #343d46;
    grid-gap: 5px;
  }
  .actionMenu-container button{
    width:100%;
  }
}



@media only screen and (max-width: 1300px) {
  .imagePlacards-container{
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 3px;
  }
}

@media only screen and (max-width: 640px) {
  .imagePlacards-container{
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 3px;
  }
}


@media only screen and (max-width: 500px) {
  .imagePlacards-container{
    grid-template-columns: 100%;
    grid-gap: 3px;
  }
}





/*imageitem*/

.image-item-container{
  position:relative;
}
.image-item-container:hover > .image-item-zoom{
 opacity: 1;
 transition: .25s;
 z-index:1;
}
.image-item-container:hover > .image-item{
  opacity: 0.5;
  display: inline-block;
  cursor: pointer;
  transition: .25s;
}
.image-item-container:hover > .image-item-save{
  opacity: 1;
  transition: .25s;
  z-index:1;
}



/*imageSaved*/

.imageSaved-item-container{
  position:relative;
  width: 100%;
}

.imageSaved-item-container > .image-item-zoom{
  font-size: 1.1em;
  opacity: 0;
  left: 45%;
  top:40%;
}
.imageSaved-item-container > .image-item-save{
  font-size: 1.1em;
  opacity: 0;
  left: 85%;
}
.imageSaved-item-container:hover  > .image-item{
  opacity: 0.5;
  display: inline-block;
  cursor: pointer;
  transition: .25s;
}


.image-item{
  height:400px;
  width: 100%;
}
.image-item-zoom{
  position:absolute;
  background: none;
  color: #fff;
  border: none;
  font-size: 1.5em;
  opacity: 0;
  left: 45%;
  top:40%;
  cursor: pointer;
  transition: .25s;

}
.image-item-zoom:hover{
  font-size: 1.8em;
}

.image-item-save{
  position:absolute;
  background:none;
  border:none;
  color: #fff;
  font-size: 1.5em;
  opacity: 0;
  left:85%;
  top:2%;
  cursor: pointer;
  transition: .25s;
}
.image-item-save:hover{
  font-size: 1.8em;
}
.imageSaved-item{
  height:100px;
  width: 100%;
}

.imageSaved-item-container:hover .image-item-zoom{
  font-size: 1em;
  opacity: 1;

  z-index:1;
}
.imageSaved-item-container:hover .image-item-zoom:hover{
  font-size: 1.2em;
}

.imageSaved-item-container:hover .image-item-save{
  font-size: 1em;
  opacity: 1;
}
.imageSaved-item-container:hover .image-item-save:hover{
  font-size: 1.2em;
  opacity: 1;
  z-index:1;
}

.keyword{
   background:	#FAF0E6;
   border:none;
   width: auto;
   margin: 10px;
   font-size: 1.5em;
   text-align: center;
   color: #2F4F4F;
   border-radius: 5px;
   cursor:pointer;
}



/*Search Field*/
.searchField{
  border: 1px solid white;
  width: 100%;
  height:45px;
  border:none;
  border-radius: 35px;
  font-size: 22px;
  text-shadow: 0px 0px 6px rgba(255,255,255,0.7);
  text-indent: 40px;
  transition: .2s linear;
}
.searchField:focus {
   border-color: #6EA2DE;
   box-shadow: 0px 0px 10px #6EA2DE;
   background-color: #DCDCDC;
}
.fa-search{
   position: absolute;
   top: 15px;
   left: 25px;
   font-size: 22px;
   color: grey;
   z-index: 1;
}

/*Action Menu*/
.action-button{
  width: 9em;
  height: 3em;
  font-size: 16px;
  text-shadow: 0px 0px 6px rgba(255,255,255,0.7);
}
.action-button-search{
    border-radius: 4px;
    background-color: #20B2AA /*HotPink*/;
    border: none;
    color: #FFFFFF;
    text-align: center;
    cursor: pointer;
    transition: all 0.2s;
}
.action-button-search span{
  cursor: pointer;
  display:inline-block;
  position: relative;
  transition: 0.2s;
}
.action-button-search span:after{
  content: '\00a0\00bb';
  position: absolute;
  opacity: 0;
  top: 0;
  right: -10px;
  transition: 0.2s;
}
.action-button-search:hover span{
  padding-right: 15px;
}
.action-button-search:hover span:after{
  opacity: 1;
  right: 0;
}
.action-button-search:hover{
    background-color: #008B8B /*DeepPink*/;
    color: #FFFFFF;
    right: -3px;
}
.action-button-search:active{
    border-radius: 4px;
    background-color: #008080 /*MediumVioletRed*/;
    border: none;
    color: #FFFFFF;
}
.action-button-save{
  background-color: Orchid;
  border-radius: 4px;
  border: none;
  color: #FFFFFF;
  transition: all 0.2s;
}
.action-button-save span{
  cursor: pointer;
  display:inline-block;
  position: relative;
  transition: 0.2s;
}

.action-button-save:hover{
  background-color: MediumOrchid;
  cursor:pointer;
  right: -3px;
}
.action-button-save:active{
  background-color: DarkOrchid;
}
.action-button-save span:after{
  content: '\00a0\2913';
  position: absolute;
  opacity: 0;
  top: -3px;
  transition: 0.2s;
}
.action-button-save:hover span{
  padding-right: 15px;
}
.action-button-save:hover span:after{
  top:0;
  opacity: 1;
}
